import {
    Component,
    h,
    Element,
    State,
    Prop,
    Event,
    EventEmitter,
    Watch,
} from '@stencil/core';
import { fetchTranslations } from '../../../utils/translation';
import { ColDef } from 'ag-grid-community';

export interface ColumnManagerChangeEvent {
    id: string;
    checked: boolean;
}

@Component({
    tag: 'at-column-manager',
    shadow: false,
})
export class AtColumnManagerComponent {
    /**
     * Column definitions used in your at-table
     */
    @Prop()
    col_defs: ColDef[];

    @Element() el: HTMLElement;
    @State() translations: any;
    @State() checkedColumns: string[] = [];

    async componentWillLoad() {
        this.translations = await fetchTranslations(this.el);
        this.initializeCheckedColumns();
    }

    @Watch('col_defs')
    colDefsChanged() {
        this.initializeCheckedColumns();
    }

    private initializeCheckedColumns() {
        if (this.col_defs) {
            this.checkedColumns = this.col_defs
                .filter((colDef) => colDef.hide !== true)
                .map((colDef) => colDef.field);
        }
    }

    /**
     * Custom event used by at-table-actions to perform ag-grid logic
     *
     * Emitted when checkbox is clicked
     */
    @Event({ eventName: 'atChange' })
    atChange: EventEmitter<ColumnManagerChangeEvent>;

    changeHandler(event: CustomEvent<string[]>) {
        const selectedColumns = event.detail || [];

        const previousColumns = new Set(this.checkedColumns);
        const currentColumns = new Set(selectedColumns);

        selectedColumns.forEach((columnId) => {
            if (!previousColumns.has(columnId)) {
                this.atChange.emit({
                    id: columnId,
                    checked: true,
                });
            }
        });

        Array.from(previousColumns).forEach((columnId) => {
            if (!currentColumns.has(columnId)) {
                this.atChange.emit({
                    id: columnId,
                    checked: false,
                });
            }
        });

        this.checkedColumns = selectedColumns;
    }

    render() {
        return (
            <at-menu
                autoclose={false}
                width="fit-content"
                align="start"
            >
                <div slot="menu-trigger">
                    <at-tooltip position="top">
                        <at-button
                            slot="tooltip-trigger"
                            type="secondaryText"
                            icon="graphic_eq"
                        />
                        <span>
                            {this.translations.ATUI.TABLE.MANAGE_COLUMNS}
                        </span>
                    </at-tooltip>
                </div>
                <at-checkbox-group
                    class="w-fit"
                    options={
                        this.col_defs
                            ? this.col_defs.map((colDef) => {
                                  return {
                                      option_id: colDef.field,
                                      label: colDef.field,
                                  };
                              })
                            : []
                    }
                    value={this.checkedColumns}
                    onAtuiChange={(event) => this.changeHandler(event)}
                />
            </at-menu>
        );
    }
}
